.navbar {
  width: 100%;
  height: 90px;
  background-color: #171717;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1rem;
  z-index: 50;
  font-family: Gotham Narrow SSm A, Gotham Narrow SSm B, Rubik, Lato,
    Lucida Grande, Lucida Sans Unicode, Tahoma, Sans-Serif;
}

.navbar__logo h2 {
  color: #f4f4f4;
  font-size: 1.2rem;
  cursor: pointer;
}
.navbar__links {
  display: flex;
  list-style: none;
  align-items: center;
}
.navbar__links > li {
  padding-left: 2.5rem;
}
.navbar__links > li > a {
  text-decoration: none;
  color: #f4f4f4;
  font-size: 1rem;
  display: flex;
  align-items: center;
}
.navbar__links > li > a > span {
  display: flex;
  align-items: center;
  margin-left: 8px;
}
.cart__link {
  background-color: #333;
  padding: 10px;
  border-radius: 8px;
}
.cart__link:hover {
  background-color: #3f3303;
  color: #f4f4f4;
}
.cartlogo__badge {
  width: 30px;
  height: 30px;
  background-color: #f4f4f4;
  border-radius: 50%;
  margin-left: 8px;
  color: #171717;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1rem;
}
.hamburger__menu {
  width: 30px;
  height: 30px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.hamburger__menu > div {
  width: 100%;
  height: 3px;
  background-color: #f4f4f4;
}
.hamburger__menu:hover > div {
  background-color: #3f3303;
}

@media (max-width: 960px) {
  .navbar__links {
    display: none;
  }
  .hamburger__menu {
    display: flex;
  }
}
@media (max-width: 500px) {
  .navbar__logo h2 {
    font-size: 1rem;
  }
}
